import noData from '@/components/noData'
import back from '@/components/back'
import { setInterval } from 'timers';

export default {
    components:{
        noData,
        back
    },
    data(){
        return{
			navArr:[],
			act:0,
			GoodsClasss:[],
			Goods:[],
			defaultImg: 'this.src="' + require('../../assets/prefRecommend/1.jpg') + '"',
			GoodsNum:0,
			ListDetails:this.$store.state.preList,
			specsShow:false,
			carShow:false,
			spcData:null,
			chooseDrShow:false,
			search: '',
			page: 1,
			haveMore: true,
			inputFlag: false,
			pageShow: false,
			showConfirm: false,
			act2:null,
			SpecialistHosGroupId:this.$store.state.preId,
			HosGroupName:'',
			list:[],
			drSearch:'',
			act3:null,
			user: this.$store.state.userInfo,
			title:"",
			actT:null,
			SpecialId:null,
			idxNum:0,
			GoodsTypeNo:'',
			istHos:false,
			yEvent:null,
			goodList:this.$store.state.goodList,
			vTop:0,
			allSpa:this.$store.state.allSpa,
			boxWidth:2500,
			AreaId:this.$store.state.inspctArea?this.$store.state.inspctArea.AreaId:null,
			AreaName:this.$store.state.inspctArea?this.$store.state.inspctArea.AreaName:null,
        }
    },
    computed:{
        totalPrice(){
            let p = 0
            if(this.ListDetails && this.ListDetails.length){
                this.ListDetails.forEach(v => {
					if(v.GoodsSource == 1){
						 p += (v.Price * v.GoodsNum)
					}else{
						p += (v.VipPrice * v.GoodsNum)
					}
                })
            }
            return p.toFixed(2)
        },
		totalM(){
			let p = 0
			if(this.ListDetails && this.ListDetails.length){
			    this.ListDetails.forEach(v => {
			        p += Number(v.RecommondAmount) * v.GoodsNum
			    })
			}
			return p
		}
    },
    created(){
// 		if(this.goodList){
// 			this.setList()
// 		}else{
// 			this.pullGoods()
// 			this.istHosGroupMember()
// 		}
//console.log(this.ListDetails)
		console.log(this.ListDetails)
		console.log(this.goodList)
		console.log(this.allSpa)
    },
    mounted(){
	   if( this.$store.state.recomMsg.DataSource == 1){
		   this.title = "保存"
	   }else{
		   this.title = "去推荐"
	   }
    },
    beforeDestroy(){
        window.onscroll = null
    },
	beforeRouteEnter(to, from, next) { //进入前    	
		next(vm => {
			vm.goodList = vm.$store.state.goodList
			if(vm.goodList){
				vm.setList()
				const secondScroll = vm.$refs.secondScroll
				setTimeout(() => {
					vm.getMore()
					vm.idxNum = vm.$store.state.scrollPo.idxNum
					vm.$refs.secondScroll.scrollTop = vm.$store.state.scrollPo.vTop
				}, 10)
			}else{
				if(vm.$store.state.inspctArea){
					vm.AreaId = vm.$store.state.inspctArea.AreaId
					vm.AreaName = vm.$store.state.inspctArea.AreaName
					vm.pullGoods(0,vm.AreaId)
				}else{
					vm.getHospitalByDrId()
				}
				vm.istHosGroupMember()
			}
		});
	},
    methods: {
		getHospitalByDrId(){
			this.$get('PlatFormAPI/Doctor/QueryHospitalByDrId',{
				DrId:this.user.Data.User.DoctorId,
			}).then(o => {
				this.AreaId = o.AreaId
				this.AreaName = o.AreaName
				this.pullGoods(this.SpecialistHosGroupId,0,o.AreaId)
			})
		},
		goArea(){
			this.province = {
				AreaId:this.AreaId,
				AreaName:this.AreaName
			}
			this.$store.dispatch('pushinspctArea',this.province)
			this.$router.push(`/GoodsArea`)
		},
		goDetail(i){
			if(i.GoodsSource != 1 && i.GoodsSource != 4){
				let a = {
					GoodsTypeNo:this.GoodsTypeNo,
					idxNum:this.idxNum,
					istHos:this.istHos,
					vTop:this.vTop
				}
				this.$store.dispatch('pushscrollPo', a)
				this.province = {
					AreaId:this.AreaId,
					AreaName:this.AreaName
				}
				this.$store.dispatch('pushinspctArea',this.province)
				this.$router.push(`/prefRecommend/preDetail?id=${i.GoodsId}&from=2`)
			}
		},
		alertMsgevt(event){
			var $ball = this.$refs.ball;
			$ball.style.top = event.pageY+'px';
			$ball.style.left = event.pageX+'px';
			$ball.style.transition = 'left 0s, top 0s';
			setTimeout(()=>{
				$ball.style.top = (window.innerHeight-50)+'px';
				$ball.style.left = '40px';
				$ball.style.zIndex = 1000;
				$ball.style.transition = 'left 1s linear, top 1s ease-in';
			}, 20)
			setTimeout(()=>{
				$ball.style.display ='none';
				$ball.style.top = window.innerHeight+'px';
				$ball.style.display ='block';
			}, 1000)
		},
		istHosGroupMember(){
			this.$get('PlatFormAPI/SpecialistHosGroup/IsSpecialistHosGroupMember',this.$pick({
				DrId:this.user.Data.User.DoctorId,
			})).then(o => {
				if(o){
					this.istHos = true
				}
			})
		},
		toPre(){
			if(this.ListDetails && this.ListDetails.length){
				this.showConfirm = true
				return
			}
			this.$store.dispatch('pushpreId',0)
			this.ListDetails = []
			this.$store.dispatch('pushpreList', [])
			this.$store.dispatch('pushgoodList', null)
			this.$store.dispatch('pushscrollPo', null)
			this.$store.dispatch('pushinspctArea',null)
			this.$router.push('/prefRecommend/preSpecialty')
		}, 
		confirm(){
			this.$store.dispatch('pushpreId',0)
			this.ListDetails = []
			this.$store.dispatch('pushpreList', [])
			this.$store.dispatch('pushgoodList', null)
			this.$store.dispatch('pushscrollPo', null)
			this.$router.push('/prefRecommend/preSpecialty')
		},
		pullGoods(num,AreaId){
			this.$post('PlatFormAPI/SpecialistHosGroup/GetMallGoods',{
				DrId:this.user.Data.User.DoctorId,
				GoodsTypeNo:this.GoodsTypeNo,
				AreaId,
				IsIncludeDoctorService:1
			}).then(o => {
				this.$store.dispatch('pushgoodList', o)
				if(!num){
					this.GoodsTypeNo = o.GoodsTypeNo
					this.navArr = o.MallGoodsTypeFirst
				}
				this.GoodsClasss = o.MallGoodsTypeSecond
				this.GoodsTypeNo = o.GoodsTypeNo
				this.Goods = o.Goods
				if(this.ListDetails && this.ListDetails.length){
					this.ListDetails.forEach((v,k) => {
					   o.Goods.forEach((i,y) => {
						   if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){
							   o.Goods.splice(y, 1, v)
						   }
					   })
					})
				}
				o.MallGoodsTypeFirst.forEach((a,w) => {
				   if(a.MallGoodsTypeFirst == this.GoodsTypeNo){
					   this.act = w
				   }
				})
				 setTimeout(() => {
				    this.getMore()
				    //this.scrollH()
					const boxW = this.$refs.boxW
					this.boxWidth = 10
					for (var i = 0; i < boxW.length; i++) {
						this.boxWidth += boxW[i].offsetWidth
					}
				}, 100)
				
				
			})
		},
		setList(){
			this.istHos = this.$store.state.scrollPo.istHos
			this.navArr = this.goodList.MallGoodsTypeFirst
			this.GoodsClasss = this.goodList.MallGoodsTypeSecond
			this.GoodsTypeNo = this.goodList.GoodsTypeNo
			this.Goods = this.goodList.Goods
			if(this.ListDetails && this.ListDetails.length){
				this.ListDetails.forEach((v,k) => {
				   this.goodList.Goods.forEach((i,y) => {
					   if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){
						   this.goodList.Goods.splice(y, 1, v)
					   }
				   })
				})
			}
			this.goodList.MallGoodsTypeFirst.forEach((a,w) => {
			   if(a.GoodsTypeNo == this.GoodsTypeNo){
				   this.act = w
			   }
			})
			setTimeout(() => {
			    this.getMore()
			    //this.scrollH()
				const boxW = this.$refs.boxW
				this.boxWidth = 10
				for (var i = 0; i < boxW.length; i++) {
					this.boxWidth += boxW[i].offsetWidth
				}
			}, 100)
		},
		navActive(i,idx){
				this.boxWidth = 10
				this.GoodsTypeNo = i.GoodsTypeNo
				this.idxNum=0
				this.act = idx
				this.SpecialId = i.SpecialistHosGroupId
				this.pullGoods(1,this.AreaId)
		},
		add(i,idx,d,$event){
			if(d){
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){   
						i.GoodsNum = v.GoodsNum + 1
						this.ListDetails.splice(k, 1, i)
					}
				})
				this.alertMsgevt($event)
			}else{
				this.yEvent = $event
				if(i.GoodsSpec && i.GoodsSpec.length > 1 && i.GoodsSource != 1){
					var t = 0
					this.ListDetails.forEach((v,k) => {
						i.GoodsSpec.forEach((a,y) => {
							if(v.GoodsSpecId == a.GoodsSpecId){
							   this.act2 = y
							   t = 1
							}
						})
					})
					if(t==0){
						this.act2 = null
					}
					this.spcData = i
					this.carShow = true
					return
				}else if(i.GoodsSource == 1){
					this.drSearch = ""
					this.spcData = i
					let a = {
						SpecialistHosGroupId:this.SpecialistHosGroupId,
						idxNum:this.idxNum,
						istHos:this.istHos,
						vTop:this.vTop
					}
					this.$store.dispatch('pushscrollPo', a)
					if(this.ListDetails && this.ListDetails.length){
						let t = 0
						this.ListDetails.forEach((v,k) => {
						    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){  
								t = 1
							}
						})
						if(t==0){
							i.DrId = 0
						}
					}else{
						i.DrId = 0
					}
					this.$store.dispatch('pushallSpa', i)
					if(this.allSpa){
						if(this.allSpa.GoodsSource == i.GoodsSource && this.allSpa.GoodsId == i.GoodsId && this.allSpa.GoodsClassId == i.GoodsClassId && this.allSpa.SpecialistGoodsId == i.SpecialistGoodsId){
						}else{
							this.$store.dispatch('pusharea',null)
						}
					}
					this.province = {
						AreaId:this.AreaId,
						AreaName:this.AreaName
					}
					this.$store.dispatch('pushinspctArea',this.province)
					this.$store.dispatch('pushpreList',this.ListDetails)
					this.$router.push('/prefRecommend/doctorList')					
// 					this.chooseDrShow = true
// 					this.pull()
// 					return
				}else if(i.GoodsSource == 4){
					if(this.ListDetails && this.ListDetails.length){
						let t = 0
						this.ListDetails.forEach((v,k) => {
						    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){   
								i.GoodsNum = v.GoodsNum + 1
								this.ListDetails.splice(k, 1, i)
								t = 1
							}
						})
						if(t == 0){
							i.GoodsNum = 1
							this.ListDetails.push(i)
						}
					}else{
						i.GoodsNum = 1
						this.ListDetails.push(i)
					}
					this.alertMsgevt($event)
				}else{
					if(this.ListDetails && this.ListDetails.length){
						let t = 0
						this.ListDetails.forEach((v,k) => {
						    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){   
								i.GoodsNum = v.GoodsNum + 1
								i.GoodsSpecId = v.GoodsSpecId
								i.GoodsSpecName = v.GoodsSpecName
								this.ListDetails.splice(k, 1, i)
								t = 1
							}
						})
						if(t == 0){
							i.GoodsNum = 1
							i.GoodsSpecId = i.GoodsSpec[0].GoodsSpecId
							i.GoodsSpecName = i.GoodsSpec[0].GoodsSpecName
							this.ListDetails.push(i)
						}
					}else{
						i.GoodsNum = 1
						i.GoodsSpecId = i.GoodsSpec[0].GoodsSpecId
						i.GoodsSpecName = i.GoodsSpec[0].GoodsSpecName
						this.ListDetails.push(i)
					}
					this.alertMsgevt($event)
				}
			}
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		addspc(a){
			if(a.GoodsNum){
				this.spcData.GoodsNum = a.GoodsNum + 1
			}else{
				this.spcData.GoodsNum = 1
			}
			this.carShow = false
			this.carShow = true
		},
		reducespc(a){
			if(this.spcData.GoodsNum <= 1){
				return
			}
			this.spcData.GoodsNum = a.GoodsNum - 1
			this.carShow = false
			this.carShow = true
		},
		sSpecName(i,idx){
			this.act2 = idx
			this.spcData.GoodsSpecId = i.GoodsSpecId
			this.spcData.GoodsSpecName = i.GoodsSpecName
			this.spcData.VipPrice = i.VipPrice
		},
		carSure(a){
			if(!a.GoodsSpecId){
				return this.$toast('请选择规格')
			}
			if(!this.spcData.GoodsNum){
				return this.$toast('请输入数量')
			}
			this.carShow = false
			if(this.ListDetails && this.ListDetails.length){
				let t = 0
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == a.GoodsSource && v.GoodsId == a.GoodsId && v.GoodsClassId == a.GoodsClassId && v.SpecialistGoodsId == a.SpecialistGoodsId){  
						t = 1
						a.GoodsNum = v.GoodsNum
						this.ListDetails.splice(k, 1, a)
					}
				})
				if(t == 0){
					a.GoodsNum = this.spcData.GoodsNum
					this.ListDetails.push(a)
				}
			}else{
				a.GoodsNum = this.spcData.GoodsNum
				this.ListDetails.push(a)
			}
			this.alertMsgevt(this.yEvent)
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		reduce(i,n){
			this.ListDetails.forEach((v,k) => {
			    if(v.GoodsSource == i.GoodsSource && v.GoodsId == i.GoodsId && v.GoodsClassId == i.GoodsClassId && v.SpecialistGoodsId == i.SpecialistGoodsId){ 
					i.GoodsNum = v.GoodsNum - 1
					if(i.GoodsNum < 1){
						this.ListDetails.splice(k, 1)
					}else{
						this.ListDetails.splice(k, 1, i)
					}
					if(!this.ListDetails.length){
						this.specsShow = false
					}
				}
			})
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		specsCar(){
			if(this.specsShow){
				this.specsShow = false
			}else{
				this.specsShow = true
			}
		},
		del(){
			this.specsShow = false
			this.ListDetails = []
			this.Goods.forEach((v,k) => {
			   v.GoodsNum = 0
			})
			this.$store.dispatch('pushallSpa',null)
			this.$store.dispatch('pusharea',null)
			this.$store.dispatch('pushpreList',[])
		},
		closeCar(a){
			this.carShow = false
			if(this.ListDetails && this.ListDetails.length){
				let t = 0
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == a.GoodsSource && v.GoodsId == a.GoodsId && v.GoodsClassId == a.GoodsClassId && v.SpecialistGoodsId == a.SpecialistGoodsId){  
						t = 1
						a.GoodsNum = v.GoodsNum
						this.ListDetails.splice(k, 1, a)
					}
				})
				if(t == 0){
					a.GoodsNum = 0
				}
			}else{
				a.GoodsNum = 0
			}
		},
		sendSave(){
			if(this.ListDetails && this.ListDetails.length){
				
			}else{
				return this.$toast('请先添加商品')
			}
			let List = []
			this.ListDetails.forEach(v => {
			    List.push({
					GoodsSource:v.GoodsSource,	//商品类型(1-平台服务，2-专科商品，3-平台商品)
					SpecialistGoodsId:v.GoodsSource == 3 ? 0:v.SpecialistGoodsId,//专科商品Id(商品类型1,2,4 必填，3传0)
					GoodsId:v.GoodsId,	//商品Id/服务项Id
					GoodsName:v.GoodsName,	//商品名称/服务项名称
					GoodsClassId:v.GoodsClassId,	//商品类型Id
					GoodsClassName:v.GoodsClassName,	//类型名称
					GoodsNum:v.GoodsNum,	//商品数量
					DrId:v.DrId || 0,
					DrName:v.DrName||'',
					GoodsSpecId:v.GoodsSpecId || "",	//商品规格Id
					GoodsSpecName:v.GoodsSpecName || "",	//规格名称
			    })
			})
			this.$bridge.callhandler('printdata','初始进入'+this.$store.state.recomMsg.DataSource)
			if(this.$store.state.recomMsg.DataSource == 1){
				this.$post('PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommendUsed',{
					GoodsRecomId:0,	//商品推荐Id
					DrId:this.user.Data.User.DoctorId,
					DrName:this.user.Data.User.DoctorName,	//医生名称
					SpecialistHosGroupId:0,	//专科医联体id
					HosGroupName:'',	//专科医联体名称
					RecommendType:2,	//推荐类型(1-专科商品，2-平台商品)
					GoodsRecomDetail:List,
				}).then(o => {
					if(!o){
						return
					}
					this.$router.push(`/prefRecommend/preEntry`)
				})
			}else{
				this.$post('PlatFormAPI/SpecialistHosGroup/SaveGoodsRecommend',{
					GoodsRecomId:0,	//商品推荐Id
					DrId:this.user.Data.User.DoctorId,
					DrName:this.user.Data.User.DoctorName,	//医生名称
					SpecialistHosGroupId:0,	//专科医联体id
					HosGroupName:'',	//专科医联体名称
					RecommendType:2,	//推荐类型(1-专科商品，2-平台商品)
					GoodsRecomDetail:List,
				}).then(o => {
					if(!o){
						return
					}
					if(this.$store.state.recomMsg.DataSource == 3){
						this.$store.dispatch('pushrecomMsg',{
							DataSource: 3,//数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
							DataId: o,//数据来源Id
							fromIM:this.$store.state.recomMsg.fromIM
						})
						this.$router.replace(`/prefRecommend/reDetail`)
					}					
				})
			}
			
		},
		searchFor(){
			let a = {
				SpecialistHosGroupId:this.SpecialistHosGroupId,
				idxNum:this.idxNum,
				istHos:this.istHos,
				vTop:this.vTop
			}
			this.$store.dispatch('pushscrollPo', a)
			this.province = {
				AreaId:this.AreaId,
				AreaName:this.AreaName
			}
			this.$store.dispatch('pushinspctArea',this.province)
			this.$router.push(`/prefRecommend/preSpecialtyList?search=${this.search}&from=2`)
		},
		pull(){
			this.pageShow = false
		    this.$post('PlatFormAPI/SpecialistHosGroup/GetSpecialistDoctorList',{
				DrId:this.user.Data.User.DoctorId,
		       SpecialistHosGroupId:this.SpecialistHosGroupId,	//专科团队Id
		       GoodsClassId:this.spcData.GoodsClassId,	//商品类型Id
		       GoodsClassCode:this.spcData.GoodsClassCode,	//商品类型Code
		       ServiceItemId:this.spcData.GoodsId,	//服务项Id
		       DoctorName:this.drSearch,	//医生名称
		       PageIndex: this.page,
		       PageSize: 100
		    }).then(o => {
		        this.list = o.ReturnData
				var t = 0
				this.ListDetails.forEach((v,k) => {
					o.ReturnData.forEach((a,y) => {
						if(v.GoodsClassId == this.spcData.GoodsClassId && v.GoodsId == this.spcData.GoodsId&& a.DrId == v.DrId){
						   this.act3 = y
						   t = 1
						}
					})
				})
				if(t==0){
					this.act3 = null
				}
				this.pageShow = true
		    })
		},
		drNo(){
			this.ListDetails.forEach((v,k) => {
				if(v.GoodsClassId == this.spcData.GoodsClassId && v.GoodsId == this.spcData.GoodsId&& this.spcData.DrId == v.DrId){
					this.ListDetails.splice(k, 1)
				}
			})
			this.$store.dispatch('pushpreList',this.ListDetails)
			this.spcData.GoodsNum = 0
			this.spcData = null
			this.chooseDrShow = false
		},
		drIcon(c,idx){
			this.act3 = idx
			this.spcData.GoodsNum = 1
			this.spcData.DrId = c.DrId
			this.spcData.DrName = c.DrName
			this.spcData.Price = c.Price
			this.spcData.PositionName = c.PositionName
			this.spcData.HospitalName = c.HospitalName
			this.spcData.RecommondAmount = c.RecommondAmount
		},
        drYes(){
			if(this.act3 == null){
				return this.$toast('请先选择医生')
			}
			this.chooseDrShow = false
			if(this.ListDetails && this.ListDetails.length){
				let t = 0
				this.ListDetails.forEach((v,k) => {
				    if(v.GoodsSource == this.spcData.GoodsSource && v.GoodsId == this.spcData.GoodsId && v.GoodsClassId == this.spcData.GoodsClassId){  
						t = 1
						this.ListDetails.splice(k, 1, this.spcData)
					}
				})
				if(t == 0){
					this.ListDetails.push(this.spcData)
				}
			}else{
				this.ListDetails.push(this.spcData)
			}
			this.alertMsgevt(this.yEvent)
			this.$store.dispatch('pushpreList',this.ListDetails)
		},
		searchFor2(){
			this.pull()
		},
        getMore(){
            //const el = this.$refs.secondScroll
			const secondScroll = this.$refs.secondScroll
            let timer = null
            secondScroll.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = secondScroll.scrollTop ||
                    	secondScroll.pageYOffset ||
                    	secondScroll.scrollTop,
                    	h = secondScroll.scrollHeight,
                    	wh = secondScroll.offsetHeight
						this.vTop = top
						var secondNav = this.$refs.secondNav
						for(var i=0;i<secondNav.length;i++){
							if(secondNav[i].offsetTop - 175 - top < 0 && secondNav[i].offsetTop - 175 - top > -secondNav[i].clientHeight){// 
								this.idxNum = i
							}
							
						}

                }, 100)
            }
        },
		goodsClassA(idx){
			var secondNav = this.$refs.secondNav
			const secondScroll = this.$refs.secondScroll
			var scH = 0
			for(var i=0;i<idx;i++){
				if(idx>0){
					scH += secondNav[i].clientHeight+10
				}
			}
 			secondScroll.scrollTop = scH
			this.idxNum = idx
		},
// 		 scrollH(){
// 			if(this.$store.state.scrollPo){
// 				//this.goodsClassA(this.$store.state.scrollPo.idxNum)
// 				this.idxNum = this.$store.state.scrollPo.idxNum
// 				const secondScroll = this.$refs.secondScroll
// 				secondScroll.scrollTop = this.$store.state.scrollPo.vTop
// 			}
// 		},
        
    }
}



